<template>
  <div class="small-title-box-sm">
    <h2>
      {{ title
      }}<el-button v-if="isBeing" @click="publicClick" class="fontRight">{{
        text
      }}</el-button>
    </h2>
  </div>
</template>

<script>
export default {
  name: "small-title",
  props: {
    title: String,
    text: String,
    isBeing: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    publicClick() {
      this.$emit("click");
    }
  }
};
</script>

<style lang="scss" scoped>
.small-title-box-sm {
  h2 {
    font-size: 14px;
    color: #505256;
    margin-bottom: 19px;
    line-height: 14px;
    padding-left: 8px;
    position: relative;
    &::before {
      content: "";
      width: 2px;
      height: 14px;
      background: #4673fd;
      position: absolute;
      top: 50%;
      left: 0;
      -webkit-transform: translateY(-50%);
      transform: translateY(-50%);
    }
    .el-button {
      border: none;
      padding: 0;
      float: right;
      color: #4673fd;
      &:hover {
        background: none;
      }
    }
    .fontRight {
      float: right;
    }
  }
}
</style>



