<template>
  <div class="universal-pagination">
    <span class="pageNumber" v-if="sum > 0"
      >共 {{ sum }} 条记录 第 {{ currentPage }} /
      {{ Math.ceil(sum / pageNum) }} 页</span
    >
    <el-pagination
      class="systemPagination"
      @size-change="pagesChange"
      @current-change="currentChange"
      :current-page="currentPage"
      :page-sizes="[20, 40, 60]"
      :page-size="pageNum"
      :pager-count="pagerCount"
      layout="sizes, prev, pager, next, jumper"
      :total="sum"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: "common-pagination",
  data() {
    return {
      pageNum: 20
    };
  },
  props: {
    sum: {
      type: Number,
      default: 0
    },
    currentPage: {
      type: Number
    },
    pagerCount: {
      type: Number,
      default: 11
    }
  },
  methods: {
    pagesChange(val) {
      this.pageNum = val;
      this.$emit("page", val);
    },
    currentChange(val) {
      this.$emit("current", val);
    }
  }
};
</script>

<style lang="scss" scoped></style>



