<template>
  <div class="common-botton-box-sm">
    <el-button
      size="small"
      type="danger"
      v-if="Number(buttonType) === 2"
      @click="handleCommand"
      >{{ text }}</el-button
    >
    <el-button
      type="primary"
      size="small"
      v-if="Number(buttonType) === 0"
      @click="handleCommand"
      >{{ text }}</el-button
    >
    <el-button
      size="small"
      plain
      @click="handleCommand"
      v-if="Number(buttonType) === 1"
      >{{ text }}</el-button
    >
  </div>
</template>

<script>
export default {
  name: "common-botton",
  props: {
    text: String,
    buttonType: {
      default: 0
    }
  },
  methods: {
    handleCommand() {
      this.$emit("approve");
    }
  }
};
</script>

<style lang="scss" scoped>
.common-botton-box-sm {
  display: inline-block;
  margin-right: 8px;
}
</style>



