<template>
  <div class="universalTable_sm">
    <el-table :data="tableData" style="width: 100%">
      <el-table-column
        v-for="(item, index) in column"
        :key="index"
        :prop="item.value"
        :label="item.name"
        :width="item.width"
        :show-overflow-tooltip="item.tooltip"
      >
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "common-table",
  props: {
    tableData: Array,
    column: Array
  }
};
</script>

<style lang="scss" scoped></style>



