<template>
  <div class="middle_sm">
    <div class="middle___hear">
      <h2>{{ title }}</h2>
      <div class="middle___hearBtn">
        <el-button type="primary" size="small" @click="publicHandle">{{
          text
        }}</el-button>
        <el-button plain size="small" v-if="isCancel" @click="publicCancel"
          >取 消</el-button
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "common-title",
  props: {
    title: String,
    isCancel: {
      type: Boolean,
      default: false
    },
    text: String
  },
  methods: {
    publicHandle() {
      this.$emit("click");
    },
    publicCancel() {
      this.$emit("cancel");
    }
  }
};
</script>

<style lang="scss" scoped>
.middle_sm {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #f5f5f5;
  z-index: 99;
}
.middle___hear {
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  height: 50px;
  background: #fff;
  padding: 0 32px;
  z-index: 99;
  // border-top: 1px solid #ECEFF3;
  h2 {
    display: inline-block;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.85);
    line-height: 50px;
    font-weight: 700;
  }
}
.middle___hearBtn {
  float: right;
  padding: 10px 0;
  display: flex;
}
</style>



